<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                                                      <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultuserusername">
        <el-input v-model="ruleForm.username" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultusermima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultusername">
        <el-input v-model="ruleForm.name" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultuserage">
        <el-input v-model="ruleForm.age" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultuserphone">
        <el-input v-model="ruleForm.phone" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultuseremail">
        <el-input v-model="ruleForm.email" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='defaultuser'" label="" prop="defaultusermoney">
        <el-input v-model="ruleForm.money" placeholder=""></el-input>
      </el-form-item>
                                                                                                                  <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                                                      if((!this.ruleForm.username) && `defaultuser` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `defaultuser` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                  if(`defaultuser` == this.tableName && this.ruleForm.age&&(!this.$validate.isIntNumer(this.ruleForm.age))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                  if(`defaultuser` == this.tableName && this.ruleForm.money&&(!this.$validate.isNumber(this.ruleForm.money))){
        this.$message.error(``);
        return
      }
                                                                                                                                          this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
