<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
                                                      <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="username">
          <el-input v-model="ruleForm.username" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="name">
          <el-input v-model="ruleForm.name" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='defaultuser'"  label="" prop="sex">
          <el-select v-model="ruleForm.sex" placeholder="">
            <el-option
                v-for="(item,index) in defaultusersexOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="age">
          <el-input v-model="ruleForm.age" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="phone">
          <el-input v-model="ruleForm.phone" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='defaultuser'" label="" prop="picture">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.picture?ruleForm.picture:''"
          @change="defaultuserpictureUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="email">
          <el-input v-model="ruleForm.email" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='defaultuser'"  label="" prop="money">
          <el-input v-model="ruleForm.money" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                                                                                  <el-form-item v-if="flag=='users'" label="" prop="username">
        <el-input v-model="ruleForm.username" 
        placeholder=""></el-input>
      </el-form-item>
      <el-col :span="24">
      <el-form-item>
        <el-button type="primary" @click="onUpdateHandler"> </el-button>
      </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
                                                                                          defaultusersexOptions: [],
                                                                                                                                                                            };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                                            this.defaultusersexOptions = ",".split(',')
                                                                                                                  },
  methods: {
                                                                                                                                                                                                                                                        defaultuserpictureUploadChange(fileUrls) {
        this.ruleForm.picture = fileUrls;
    },
                                                                                            onUpdateHandler() {
                                                      if((!this.ruleForm.username)&& 'defaultuser'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'defaultuser'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                  if( 'defaultuser' ==this.flag && this.ruleForm.age&&(!isIntNumer(this.ruleForm.age))){
       this.$message.error(``);
        return
      }
                                                                                                                                                                                                                  if( 'defaultuser' ==this.flag && this.ruleForm.money&&(!isNumber(this.ruleForm.money))){
        this.$message.error(``);
        return
      }
                                                                                                                                          this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
